const demo = [{
		username: "昵称",
		userpic: "/static/default.png",
		newstime: "2020-01-21 下午15:51",
		isFollow: false,
		title: "我是标题",
		titlepic: "/static/demo/1.jpg",
		support: {
			type: "support",
			support_count: 1,
			unsupport_count: 2
		},
		comment_count: 2,
		share_count: 2
	},
	{
		username: "昵称",
		userpic: "/static/default.png",
		newstime: "2020-01-21 下午15:51",
		isFollow: false,
		title: "我是标题",
		titlepic: "",
		support: {
			type: "unsupport",
			support_count: 1,
			unsupport_count: 2
		},
		comment_count: 2,
		share_count: 2
	},
	{
		username: "昵称",
		userpic: "/static/default.png",
		newstime: "2020-01-21 下午15:51",
		isFollow: false,
		title: "我是标题",
		titlepic: "/static/demo/1.jpg",
		support: {
			type: "",
			support_count: 1,
			unsupport_count: 2
		},
		comment_count: 2,
		share_count: 2
	}			
];

import commonList from '@/components/common/common-list.vue';
import loadMore from '@/components/common/load-more.vue';
import uniPopup from '@/components/uni-ui/uni-popup/uni-popup.vue';
export default {
	components:{
		commonList,
		loadMore,
		uniPopup
	},
	data() {
		return {
			tabIndex:0,
			tabBars:[{
				name:"首页"
			},{
				name:"书单",
				list:[],
				// 1.上拉加载更多  2.加载中... 3.没有更多了
				loadmore:"上拉加载更多",
			},{
				name:"帖子",
				list:demo,
				// 1.上拉加载更多  2.加载中... 3.没有更多了
				loadmore:"上拉加载更多",
			}],
		}
	},
	onNavigationBarButtonTap() {
		this.$refs.popup.open()
	},
	computed: {
		list() {
			return this.tabBars[this.tabIndex].list 
		},
		loadmore() {
			return this.tabBars[this.tabIndex].loadmore 
		}
	},
	methods: {
		changeTab(index){
			this.tabIndex = index
		},
		// 关注
		follow(e){
			// 拿到当前的选项卡对应的list
			let list = this.tabBars[this.tabIndex].list
			list[e].isFollow = true
			uni.showToast({ title: '关注成功' })
		},
		// 顶踩操作
		doSupport(e){
			// 拿到当前的选项卡对应的list
			let list = this.tabBars[this.tabIndex].list
			// 拿到当前对象
			let item = list[e.index]
			let msg = e.type === 'support' ? '顶' : '踩'
			// 之前没有操作过
			if (item.support.type === '') {
				item.support[e.type+'_count']++
			} else if (item.support.type ==='support' && e.type === 'unsupport') {
				// 顶 - 1
				item.support.support_count--;
				// 踩 + 1
				item.support.unsupport_count++;
			} else if(item.support.type ==='unsupport' && e.type === 'support'){ 					// 之前踩了
				// 顶 + 1
				item.support.support_count++;
				// 踩 - 1
				item.support.unsupport_count--;
			}
			item.support.type = e.type
			uni.showToast({ title: msg + '成功' });
		},
	}
}
